import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import HomePageContainer from './containers/pages/HomePageContainer';
// import GenerateReportContainer from './containers/pages/GenerateReportContainer';

import MainLayout from './containers/layouts/MainLayoutContainer';

class Routes extends Component {
    render() {
        return (
            <Router>
                <MainLayout>
                    <Switch>
                        <Route exact path='/' component={HomePageContainer} />
                        {/* <Route path='/authors' component={AuthorsPageContainer} />
                        <Route path='/authors/:authorId' component={SingleAuthorPAgeContainer} /> */}
                    </Switch>
                </MainLayout>
            </Router>
        );
    }
}

export default Routes;



